import { useNavigate } from "react-router";
import { imageUrl } from "../../utils/axios"; // make sure path is correct
import "./CategoryCard.css";

const CategoryCard = ({ id, image, title, rating, unit }) => {
  const navigate = useNavigate();

  return (
    <div onClick={() => navigate(`/category/${id}`)} className="category-card">
      <div className="card-top">
        <img
          src={
            image
              ? image.startsWith("http")
                ? image
                : `${imageUrl}${image}`
              : require("./item.png")
          }
          alt={title}
          className="card-img"
        />
        <div className="card-rating">⭐ {rating}/5</div>
      </div>
      <div className="card-info">
        <h3 className="card-title">{title}</h3>

        <div className="card-bottom flex">
          <span className="new-price">{unit}</span>

          <button className="add-btn">
            زيارة{" "}
            <span className="plus">
              <i className="fas fa-arrow-left"></i>
            </span>
          </button>
        </div>
      </div>
    </div>
  );
};

export default CategoryCard;
