import { useQuery } from "@tanstack/react-query";
import CategoryCard from "./CategoryCard";
import { imageUrl } from "../../utils/axios";
import APIClient from "../../utils/ApiClient";
import Skeleton from "../../components/skeleton/Skeleton";
import RepeatChildren from "./../../components/RepeatChildren";

const apiClient = new APIClient("types");

const Home = () => {
  // Fetch categories (types)
  const { data, isLoading, isError } = useQuery({
    queryKey: ["types"],
    queryFn: () => apiClient.getAll({ limit: 100 }),
    keepPreviousData: true,
  });

  if (isError) return <p className="error-text">Failed to load categories</p>;

  const types = data?.data || [];

  return (
    <div className="container flex justify-start">
      {types.map((type) => (
        <CategoryCard
          key={type?._id}
          id={type?._id}
          image={
            type?.image
              ? type?.image.startsWith("http")
                ? type?.image
                : `${imageUrl}${type?.image}`
              : null
          }
          title={type?.name}
          rating={5}
          unit={type?.itemsCount}
        />
      ))}
      {isLoading && (
        <RepeatChildren count={5} className="category-card">
          <div
            className="flex"
            style={{ justifyContent: "space-between", marginBottom: "20px" }}
          >
            <Skeleton
              width="90px"
              height="90px"
              style={{
                borderRadius: "50%",
                marginTop: "-60px",
                backgroundColor: "var(--body-color)",
              }}
            />
            <Skeleton
              width="60px"
              height="15px"
              style={{ backgroundColor: "var(--section-color)" }}
            />
          </div>
          <Skeleton
            height="25px"
            style={{
              marginBottom: "20px",
              backgroundColor: "var(--section-color)",
            }}
          />
          <Skeleton
            height="20px"
            width="50%"
            style={{ backgroundColor: "var(--section-color)" }}
          />
        </RepeatChildren>
      )}
    </div>
  );
};

export default Home;
